.appWrapper {
    display: grid;
    width: 1200px;
    min-height: 100vh;
    margin: 0 auto;
    grid-template:
            "h h h" 100px
            "n c c" 3fr
            "n c c" 5fr / 1fr 3fr 3fr;
    grid-gap: 5px;

}

.appWrapperContent {
    grid-area: c;
    background-color: #f1ff99;
    /*background-color: #EBF2FA;*/
    /*background-color: #F6F7EB;*/
}



